import * as React from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import '../styles/back.css';
export default class  extends React.Component<any, any> {
  constructor(props: any) {
    super(props);
    this.state = {
      name: '',
      nameError: false,
      password: '',
      pwdError: false
    };
  }

  handleChange = (name: any) => (event: any) => {
    this.setState({
      [name]: event.target.value,
    });
  };

  render() {
    return (
      <section className="back-sec">
        <section className="back-cont">
          <h3>后台登录</h3>
          <form className="back-form" noValidate autoComplete="off">
            <TextField
              error={this.state.nameError}
              required
              id="standard-name"
              label="账号"
              rows={10}
              className="back-form-name"
              value={this.state.name}
              onChange={this.handleChange('name')}
              margin="normal"
              fullWidth
            />
            <TextField
              error={this.state.pwdError}
              required
              id="standard-password"
              label="密码"
              className="back-form-pwd"
              value={this.state.password}
              onChange={this.handleChange('password')}
              margin="normal"
              type="password"
              fullWidth
            />
          </form>
          <Button variant="outlined" color="primary" className="back-btn">登录</Button>
        </section>
      </section>
    );
  }
}